<template>
    <div>
        首页----{{ str }}
        <br />
        测试 ---- {{ str1 }}
    </div>
</template>

<script>
import bus from '@/utils/eventBus';
export default {
    data() {
        return {
            str: '',
            str1: ""
        }
    },
    mounted() {
        this.$subscribe.on("handleSearch", (data) => {
            this.str = data
        })
        bus.$on("testSearch", (data) => {
            this.str1 = data
        })
    },
    beforeDestroy() {
        this.$subscribe.off("handleSearch")
        bus.$off("testSearch")
    }
}
</script>

<style></style>